<template>
    <!-- 热门城市和城市列表的组件 加载完成再显示 -->
  <div class="area" ref="area_scroll" v-if="cityInfo">
      <div class="scroll_wrap">
      <!-- 热门城市 -->
      <div class="hot_wrap citylist">
        <div class="title">热门城市</div>
        <ul class="hot_city">
          <li
            @click="$emit('selectCity',item)"
            v-for="(item,index) in cityInfo.hotCities"
            :key="index"
          >{{item.name}}</li>
        </ul>
      </div>
      
      <!-- 所有城市 -->
      <div class="city_wrap">
        <!-- 循环按字母排序的key -->
        <div class="city_content citylist" v-for="(item,index) in keys" :key="index">
          <div class="title">{{item}}</div>
          <!-- 根据字母key展示城市名 -->
          <ul>
            <li
              @click="$emit('selectCity',city)"
              v-for="(city,index) in cityInfo[item]"
              :key="index"
            >{{city.name}}</li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 右侧字母表 -->
    <div class="area_keys">
      <ul>
        <li @click="selectKey(0)">#</li>
        <li @click="selectKey(index+1)" v-for="(item,index) in keys" :key="index">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
//引入一个插件，用于滚动事件
import BScroll from "better-scroll";
export default {
    name: "Alphabet",
    data(){
      return {
        scroll: null
      }
    },
    props: {
        cityInfo: Object,
        keys: Array,
    },
    methods: {
      //自定义一个滚动方法
      initScroll() {
        this.scroll = new BScroll(this.$refs.area_scroll, {
        click: true
      });
      },
      selectKey(index) {
        // console.log(this.$refs.area_scroll.getElementsByClassName("citylist"));
        const citylist = this.$refs.area_scroll.getElementsByClassName("citylist");
        //根据下标滚动到相应的实例
        let el = citylist[index];
        console.log(el);
        //添加滚动事件,this.scroll在initScroll中被实例化了
        // 为什么此处无效-因为没有限制页面的高度，不限制高度会与scroll冲突，后来发现限制了，但是index页面的高度calc()写法不对，忘记用空格来区分运算式了
        this.scroll.scrollToElement(el, 250);
      },
    }
};
</script>

<style scoped>
.area {
  margin-top: 10px;
  box-sizing: border-box;
  padding: 0 16px;
  background: #fff;
  height: calc(100% - 65px);
  overflow: hidden;
}
.scroll_wrap {
  background: #fff;
  overflow: auto;
}
.title {
  color: #aaa;
  padding: 15px 10px;
}
.hot_city {
  padding: 0 16px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.hot_city li {
  width: 30%;
  background: #f1f1f1;
  margin: 0 10px 10px 0;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
}
.city_content li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
.area_keys {
  position: fixed;
  right: 0;
  top: 25%;
  color: #aaa;
  font-size: 12px;
  line-height: 1.4;
  text-align: center;
  padding: 0 5px;
}
</style>