<template>
  <div class="city">
    <div class="search_wrap">
      <div class="search">
        <i class="fa fa-search"></i>
        <input v-model="city_val" type="text" placeholder="输入城市名">
      </div>
      <button @click="$router.push({name:'address',params:{city:city}})">取消</button>
    </div>
    <!-- 添加滚动时，必须将高度限定为屏幕高，否则会因dom较长而被动可滑动，形成冲突 -->
    <div style="height:100%" v-if="searchList.length ==0">
        <div class="location">
           <Location @click="selectCity({name:city})" :address="city"/>
        </div>
        <Alphabet @selectCity="selectCity" ref="allcity" :cityInfo="cityInfo" :keys="keys" />
    </div>
    <!-- 检索城市的列表 -->
    <div class="search_list" v-else>
      <ul>
        <li @click="selectCity(item)" v-for="(item,index) in searchList" :key="index">{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Location from "../components/Location.vue";
import Alphabet from "../components/Alphabet.vue";
export default {
    name: "City",
    data(){
        return {
            city_val: "",
            cityInfo: null,
            keys: [],
            allCities: [],
            searchList: [],
        }
    },
    computed: {
      address(){
        return this.$store.getters.address;
      },
      city(){//返回城市或者省份
        return (this.$store.getters.location.addressComponent.city || this.$store.getters.location.addressComponent.province);
      }
    },
    components: {
        Location,
        Alphabet,
    },
    created() {//函数钩子，获取城市信息
        this.getCityInfo();
    },
    watch: {
      city_val(){//监听下输入的值
        this.searchCity();
      }
    },
    methods: {
        getCityInfo() {//课程自己设计的接口
            this.$axios("/api/posts/cities")
            .then(res => {
                // console.log(res.data);//从a-z的城市
                this.cityInfo = res.data;
                //处理key值
                this.keys = Object.keys(res.data);
                this.keys.pop();//把数据最后的hotcities移除，只保留A-Z
                //key排序
                this.keys.sort();
                this.$nextTick(() => {
                  this.$refs.allcity.initScroll();
                });
                //获取首字母相同的所有城市
                this.keys.forEach(key => {
                  this.cityInfo[key].forEach( city => {
                    this.allCities.push(city);
                  });
                });
            }).catch(err => {
                console.log(err);
            })
        },
        selectCity(city) {
          //跳转到address页面
          this.$router.push({name:"address",params:{city:city.name}});
        },
        searchCity(){
          if(!this.city_val){
            //当前输入为空，则显示为空
            this.searchList = [];
          }else{
            //根据输入内容检索城市
            this.searchList = this.allCities.filter(item => {
              //过滤出匹配的城市名
              return item.name.indexOf(this.city_val) != -1;
            });
          }
        }
    }

}
</script>

<style scoped>
.city {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}
.search_wrap {
  position: fixed;
  top: 0;
  height: 45px;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  padding: 3px 16px;
  display: flex;
  justify-content: space-between;
}
.search {
  background-color: #eee;
  border-radius: 10px;
  line-height: 40px;
  width: 85%;
  box-sizing: border-box;
  padding: 0 16px;
}
.search input {
  background: #eee;
  outline: none;
  border: none;
  margin-left: 5px;
}
.search_wrap button {
  outline: none;
  color: #009eef;
  background: transparent;
  border: none;
}

.location {
  background: #fff;
  padding: 8px 16px;
  height: 65px;
  box-sizing: border-box;
}

.search_list {
  background: #fff;
  padding: 5px 16px;
}
.search_list li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
</style>